/**
 * Status Page Styles
 *
 * Uses Bootstrap semantic colors:
 * - success for operational (green)
 * - warning for degraded (amber)
 * - danger for major outage (red)
 * - info for maintenance (blue)
 */
.status-pulse {
  width: 8px;
  height: 8px;
}

.status-banner.status-operational {
  background: linear-gradient(135deg, rgba(var(--bs-success-rgb), 0.1) 0%, rgba(var(--bs-success-rgb), 0.05) 100%);
  border-left: 4px solid var(--bs-success) !important;
}
.status-banner.status-degraded {
  background: linear-gradient(135deg, rgba(var(--bs-warning-rgb), 0.1) 0%, rgba(var(--bs-warning-rgb), 0.05) 100%);
  border-left: 4px solid var(--bs-warning) !important;
}
.status-banner.status-major {
  background: linear-gradient(135deg, rgba(var(--bs-danger-rgb), 0.1) 0%, rgba(var(--bs-danger-rgb), 0.05) 100%);
  border-left: 4px solid var(--bs-danger) !important;
}
.status-banner.status-maintenance {
  background: linear-gradient(135deg, rgba(var(--bs-info-rgb), 0.1) 0%, rgba(var(--bs-info-rgb), 0.05) 100%);
  border-left: 4px solid var(--bs-info) !important;
}

.uptime-bars {
  height: 32px;
  flex-wrap: nowrap;
}
@media (max-width: 767.98px) {
  .uptime-bars {
    height: 24px;
    gap: 1px !important;
  }
  .uptime-bars .uptime-bar:nth-child(-n+60) {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 991.98px) {
  .uptime-bars {
    height: 28px;
  }
  .uptime-bars .uptime-bar:nth-child(-n+30) {
    display: none;
  }
}

.uptime-bar {
  flex: 1 1 0;
  min-width: 0;
}
.uptime-bar.status-operational {
  background-color: var(--bs-success);
}
.uptime-bar.status-degraded {
  background-color: var(--bs-warning);
}
.uptime-bar.status-major {
  background-color: var(--bs-danger);
}
.uptime-bar.status-maintenance {
  background-color: var(--bs-info);
}
.uptime-bar.status-unknown {
  background-color: var(--bs-secondary-bg);
}
.uptime-bar:hover {
  transform: scaleY(1.2);
  opacity: 0.8;
}

.uptime-bar-legend {
  width: 16px;
  height: 16px;
}
.uptime-bar-legend.status-operational {
  background-color: var(--bs-success);
}
.uptime-bar-legend.status-degraded {
  background-color: var(--bs-warning);
}
.uptime-bar-legend.status-major {
  background-color: var(--bs-danger);
}
.uptime-bar-legend.status-maintenance {
  background-color: var(--bs-info);
}

.incident-card,
.maintenance-card {
  border-left: 4px solid var(--bs-border-color) !important;
}
.incident-card.status-operational, .incident-card.status-resolved,
.maintenance-card.status-operational,
.maintenance-card.status-resolved {
  border-left-color: var(--bs-success) !important;
}
.incident-card.status-degraded,
.maintenance-card.status-degraded {
  border-left-color: var(--bs-warning) !important;
}
.incident-card.status-major,
.maintenance-card.status-major {
  border-left-color: var(--bs-danger) !important;
}
.incident-card.status-maintenance,
.maintenance-card.status-maintenance {
  border-left-color: var(--bs-info) !important;
}

.incident-timeline {
  position: relative;
  padding-left: 1.5rem;
}
.incident-timeline::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: var(--bs-border-color);
}
.incident-timeline .timeline-item {
  position: relative;
}
.incident-timeline .timeline-item::before {
  content: "";
  position: absolute;
  left: -1.5rem;
  top: 0.5rem;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: var(--bs-secondary-bg);
  border: 2px solid var(--bs-border-color);
  transform: translateX(-3px);
}
.incident-timeline .timeline-item.status-investigating::before {
  background-color: var(--bs-warning);
  border-color: var(--bs-warning);
}
.incident-timeline .timeline-item.status-identified::before {
  background-color: var(--bs-danger);
  border-color: var(--bs-danger);
}
.incident-timeline .timeline-item.status-monitoring::before {
  background-color: var(--bs-info);
  border-color: var(--bs-info);
}
.incident-timeline .timeline-item.status-resolved::before {
  background-color: var(--bs-success);
  border-color: var(--bs-success);
}

.uptime-tooltip {
  position: fixed;
  z-index: 1000;
  background-color: var(--bs-body-bg);
  border: 1px solid var(--bs-border-color);
  box-shadow: var(--bs-box-shadow);
  pointer-events: none;
  white-space: nowrap;
}
.uptime-tooltip .status-dot {
  width: 8px;
  height: 8px;
}
.uptime-tooltip .status-dot.status-operational {
  background-color: var(--bs-success);
}
.uptime-tooltip .status-dot.status-degraded {
  background-color: var(--bs-warning);
}
.uptime-tooltip .status-dot.status-major {
  background-color: var(--bs-danger);
}
.uptime-tooltip .status-dot.status-maintenance {
  background-color: var(--bs-info);
}